import React from 'react'

export default function ShortMenu ({toggleClick}) {
    return (
        <div className="grid grid-rows-3 text-center items-center bg-gray-800 top-20 z-20 sticky" onClick={toggleClick}>
            <a href="#projects" className="mb-4 hover:text-white">
              Proyectos
            </a>
            <a href="#skills" className="mb-4 hover:text-white">
              Habilidades
            </a>
            <a href="#contact" className="mb-4 hover:text-white">
              Contactame
            </a>            
        </div>
    )
}