import React from "react";

const BackToTop = () => {

  function handleClick(e) {
    e.preventDefault();
    window.scroll({ top: 0, left: 0, behavior: "smooth" });
  }

  return (
    <div className="fixed z-30 bottom-0 right-0 mr-6 mb-6">
      <a
        id="backToTop"
        href="#about"
        data-disable="true"
        className="{visible ? 'bg-opacity-60' : 'bg-opacity-0'}
            text-white block bg-green-500 bg-opacity-60 rounded-md hover:bg-opacity-80 block"
        onClick={handleClick}
      >
        <span className="hidden">Ir al inicio</span>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          viewBox="0 0 24 24"
          className="h-10 w-10"
        >
          <path
            fill="currentColor"
            d="M7.41,15.41L12,10.83L16.59,15.41L18,14L12,8L6,14L7.41,15.41Z"
          />
        </svg>
      </a>
    </div>
  );
};

export default BackToTop;
