import React from "react";

export default function Navbar({toggleOpen}) {
  return (
    <header className="bg-gray-800 sticky top-0 z-20">
      <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
          <a href="#about" className="title-font font-medium text-white mb-4 ml-3 text-xl md:mb-0">
            Flavio Coscarella
          </a>
        <div className="px-4 cursor-pointer md:hidden top-0 z-10" onClick={toggleOpen}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M4 6h16M4 12h16M4 18h16"
            />
          </svg>
        </div>
        <div className="pr-8 hidden md:block">
          <nav className="md:mr-auto md:ml-4 md:py-1 md:pl-4 md:border-l md:border-gray-700	flex flex-wrap items-center text-base justify-center">
            <a href="#projects" className="mr-5 hover:text-white">
              Proyectos
            </a>
            <a href="#skills" className="mr-5 hover:text-white">
              Habilidades
            </a>
            <a href="#contact" className="mr-5 hover:text-white">
              Contactame
            </a>
          </nav>
        </div>
      </div>
    </header>
  );
}
