import { ChipIcon } from "@heroicons/react/solid";
import React from "react";
import { skills } from "../SkillList";

export default function Skills() {
  return (
    <section id="skills">
      <div className="container px-5 py-10 mx-auto">
        <div className="text-center mb-20">
          <ChipIcon className="w-10 inline-block mb-4" />
          <h1 className="sm:text-4xl text-3xl font-medium title-font text-white mb-4">
            Habilidades y Tecnologías
          </h1>
          <p className="text-base leading-relaxed xl:w-2/4 lg:w-3/4 mx-auto">
            Actualmente estas son las tecnologías de las cuales poseo conocimiento y/o manejo, gradualmente iré agregando más, y a su vez, perfeccionando las habilidades que ya poseo, ya que entiendo que trabajar con todo lo relacionado a la programación requiere un constante aprendizaje.
          </p>
        </div>
        <div className="flex flex-wrap lg:w-4/5 sm:mx-auto sm:mb-2 -mx-2">
          {skills.map((skill) => (
            <div key={skill} className="p-2 sm:w-1/2 w-full">
              <div className="bg-gray-800 rounded flex p-4 h-full items-center">
                <img
                  alt="skill"
                  className="w-6 h-6 flex-shrink-0 mr-4"
                  src={skill.image}
                />
                <span className="title-font font-medium text-white">
                  {skill.name}
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}